import React, { Component } from "react";
import { connect } from "react-redux";
import Tweet from "./tweet";

class Dashboard extends Component {
  render() {
    let { tweetIds, pages } = this.props;
    // console.log(tweetIds);
    let beginIndex = parseInt(pages.split("/")[2]);
    if (isNaN(beginIndex)) {
      beginIndex = 1;
    }
    tweetIds = tweetIds.splice(20 * (beginIndex - 1), 20);
    // console.log(beginIndex);
    // for (let i = 20 * (beginIndex - 1); i < 20 * beginIndex; i++) {}
    return (
      <div>
        {/* <h3 className="center">Your Timeline</h3> */}
        <ul className="dashboard-list">
          {tweetIds.map((id) => (
            <li key={id}>
              <Tweet id={id} />
            </li>
          ))}
        </ul>
      </div>
    );
  }
}

function mapStateToProps({ tweets }, props) {
  // console.log(props);
  return {
    tweetIds: Object.keys(tweets).sort(
      (a, b) => tweets[b].timestamp - tweets[a].timestamp
    ),
    pages: props.location.pathname,
  };
}

export default connect(mapStateToProps)(Dashboard);
