import React, { Component } from "react";
import { handleUpdateTweet } from "../actions/tweets";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
// import { NavLink } from "react-router-dom";

class SearchBar extends Component {
  _handleKeyDown = (e) => {
    if (e.key === "Enter") {
      const keyword = e.target.value;
      //   keywdSearch(keyword, 10);
      const { dispatch } = this.props;
      const count = document.getElementById("max-count").value;
      dispatch(handleUpdateTweet(keyword, count)).then(() => {
        console.log("all done");
      });
      console.log("keyword: " + keyword);
    }
  };
  _onFocus = (event) => {
    let par = event.target.parentNode;
    par = par.querySelector("i");
    par.classList.remove("search");
    par.classList.add("times");
    par.classList.add("circle");
    // console.log(par);
  };
  _onBlur = (event) => {
    let par = event.target.parentNode;
    par = par.querySelector("i");
    par.classList.add("search");
    par.classList.remove("times");
    par.classList.remove("circle");
    // console.log(par);
  };

  _clearInput = (e) => {
    const stat = document.querySelector("#inputField").classList;
    if (stat.contains("circle")) {
      //
      const inputField = document.querySelector("input.prompt");

      inputField.value = "";
    }
  };
  render() {
    return (
      <div className="ui search">
        <div class="ui icon input">
          <i id="inputField" class="search icon" onClick={this._clearInput}></i>
          <input
            class="prompt"
            type="text"
            placeholder="Search..."
            onKeyDown={this._handleKeyDown}
            onFocus={this._onFocus}
            onBlur={this._onBlur}
          />
        </div>
        <div class="results"></div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  // console.log(id);
  return {
    value: state.value,
  };
}

// function mapDispatchToProps(dispatch) {
//   return {
//     dispatch,
//   };
// }
export default withRouter(connect(mapStateToProps)(SearchBar));
