import {
  RECEIVE_TWEETS,
  TOGGLE_TWEETS,
  ADD_TWEET,
  UPDATE_TWEETS,
} from "../actions/tweets";

export default function tweets(state = {}, action) {
  // console.log(action);
  // console.log(state);
  switch (action.type) {
    case RECEIVE_TWEETS:
      return {
        ...state,
        ...action.tweets,
      };
    case TOGGLE_TWEETS:
      return {
        ...state,
        [action.id]: {
          ...state[action.id],
          likes: action.hasLiked
            ? state[action.id].likes.filter((uid) => uid !== action.authedUser)
            : state[action.id].likes.concat([action.authedUser]),
        },
      };
    case ADD_TWEET:
      const { tweet } = action;

      let replyingTo = {};
      if (tweet.replyingTo !== null) {
        replyingTo = {
          [tweet.replyingTo]: {
            ...state[tweet.replyingTo],
            replies: state[tweet.replyingTo].replies.concat([tweet.id]),
          },
        };
      }
      return {
        ...state,
        [tweet.id]: tweet,
        ...replyingTo,
      };
    case UPDATE_TWEETS:
      return {
        ...action.tweets,
      };
    default:
      return state;
  }
}
