import React, { Component } from "react";
import { connect } from "react-redux";
import { formatTweet, formatDate } from "../utils/helpers";
import {
  TiArrowBackOutline,
  TiHeartOutline,
  TiHeartFullOutline,
  TiArrowForwardOutline,
} from "react-icons/ti";
import { handleToggleTweet } from "../actions/tweets";
import { Link, withRouter } from "react-router-dom";

class Tweet extends Component {
  toParent = (e, id) => {
    e.preventDefault();
    this.props.history.push(`/tweet/${id}`);
  };

  handleLike = (e) => {
    e.preventDefault();
    // TODO: Handle like tweet  *DONE*
    const { authedUser, dispatch, tweet } = this.props;
    // console.log(tweet);
    dispatch(
      handleToggleTweet({
        id: tweet.id,
        hasLiked: tweet.hasLiked,
        authedUser,
      })
    );
  };

  render() {
    const { tweet } = this.props;

    if (tweet === null) {
      return <p>this tweet doesn't existed</p>;
    }

    const {
      name,
      id,
      timestamp,
      text,
      avatar,
      likes,
      replies,
      hasLiked,
      parent,
      uid,
    } = tweet;

    return (
      <>
        <div className="tweet">
          <div className="avatar">
            <Link
              to={{ pathname: "https://twitter.com/" + uid }}
              target="_blank"
            >
              <img src={avatar} alt={`avatar of ${name}`} />
            </Link>
          </div>

          <div className="tweet-info">
            <div>
              <span>{name}</span>
              <span className="userid">@{uid}</span>
              <div>{formatDate(timestamp)}</div>
              {/* {parent && (
                  <button
                  className="replying-to"
                  onClick={(e) => this.toParent(e, parent.id)}
                  >
                  Replying to @{parent.author}
                  </button>
                )} */}
              <p>{text}</p>
            </div>
            <div className="tweet-icons">
              <TiArrowBackOutline className="tweet-icon" />
              <span>{replies !== 0 && replies}</span>
              <button className="heart-button" onClick={this.handleLike}>
                {hasLiked ? (
                  <TiHeartFullOutline color="#e0245e" className="tweet-icon" />
                ) : (
                  <TiHeartOutline className="tweet-icon" />
                )}
              </button>
              <span>{likes > 0 && likes}</span>
            </div>
          </div>
          <div className="orig-tweet">
            <Link
              to={{
                pathname: "https://twitter.com/" + uid + "/status/" + id,
              }}
              target="_blank"
            >
              <div class="ui icon button">
                <TiArrowForwardOutline class="orig-icon"></TiArrowForwardOutline>
              </div>
            </Link>
          </div>
        </div>
      </>
    );
  }
}

function mapStateToProps({ authedUser, users, tweets }, { id }) {
  const tweet = tweets[id];
  const parentTweet = tweet ? tweets[tweet.replyingTo] : null;

  return {
    authedUser,
    tweet: tweet
      ? formatTweet(tweet, users[tweet.author], authedUser, parentTweet)
      : null,
  };
}

export default withRouter(connect(mapStateToProps)(Tweet));
