import { saveLikeToggle, saveTweet, updateData } from "../utils/api";
import { showLoading, hideLoading } from "react-redux-loading";
import { receiveUsers } from "../actions/users";
export const RECEIVE_TWEETS = "RECEIVE_TWEETS";
export const TOGGLE_TWEETS = "TOGGLE_TWEETS";
export const ADD_TWEET = "ADD_TWEET";
export const UPDATE_TWEETS = "UPDATE_TWEET";

function addTweet(tweet) {
  return {
    type: ADD_TWEET,
    tweet,
  };
}

export function handleAddTweet(text, replyingTo) {
  return (dispatch, getState) => {
    const { authedUser } = getState();

    dispatch(showLoading());
    return saveTweet({
      text,
      author: authedUser,
      replyingTo,
    })
      .then((tweet) => dispatch(addTweet(tweet)))
      .then(() => dispatch(hideLoading()));
  };
}

function toggleTweet({ id, authedUser, hasLiked }) {
  //   console.log(hasLiked);
  return {
    type: TOGGLE_TWEETS,
    id,
    hasLiked,
    authedUser,
  };
}

export function handleToggleTweet(info) {
  return (dispatch) => {
    dispatch(toggleTweet(info));

    return saveLikeToggle(info).catch((e) => {
      console.warn("error at handleToggleTweet: ", e);
      dispatch(toggleTweet(info));
      alert("the was an error liking the tweet. Try again.");
    });
  };
}

export function receiveTweets(tweets) {
  return {
    type: RECEIVE_TWEETS,
    tweets,
  };
}

function updateTweets(tweets) {
  return {
    type: UPDATE_TWEETS,
    tweets,
  };
}

export function handleUpdateTweet(keywords, count) {
  return (dispatch) => {
    dispatch(showLoading());
    // console.log(users);
    return updateData(keywords, count)
      .then((data) => {
        dispatch(receiveUsers(data["users"]));
        dispatch(updateTweets(data["tweets"]));
      })
      .then(() => dispatch(hideLoading()));
  };
}
