import React, { Fragment } from "react";
import { connect } from "react-redux";
import { handleInitialDate } from "../actions/shared";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Dashboard from "./dashboard";
import LoadingBar from "react-redux-loading";
// import NewTweet from "./newTweet";
import TweetPage from "./tweetPage";
import Likes from "./Likes";
import Nav from "./nav";
import Menu from "./menu";
import SearchBar from "./searchBar";
import Pages from "./pages";

class App extends React.Component {
  componentDidMount() {
    this.props.dispatch(handleInitialDate());
  }

  render() {
    return (
      <Router>
        <Fragment>
          <LoadingBar />
          <div className="container">
            <div className="nav">
              <Nav />
              <SearchBar></SearchBar>
              <Menu />
            </div>
            {this.props.loading ? null : (
              <div>
                <Route path="/pages/:pages" exact component={Dashboard} />
                {/* <Route path="/pages/2" component={Dashboard} /> */}
                <Route path="/" exact component={Dashboard} />
                <Route path="/tweet/:id" component={TweetPage} />
                {/* <Route path='/new' component={NewTweet}/> */}
                <Route path="/likes" component={Likes} />
              </div>
            )}
            <Pages />
          </div>
        </Fragment>
      </Router>
    );
  }
}

function mapStateToProps(state) {
  return {
    loading: state.authedUser === null,
    pages: Object.keys(state.tweets).length / 20 + 1,
  };
}

export default connect(mapStateToProps)(App);
