export async function _updateData(keyword, count) {
  // console.log(keyword + " " + count);
  users = {};
  tweets = {};
  await fetch("http://localhost:5000/flask/search", {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Accept: "application/json",
    },
    body: JSON.stringify({ keyword: keyword, count: count }),
  })
    .then((res) => res.json())
    .then((data) => {
      // console.log(data);
      // _updateData(data["users"], data["tweets"]);
      for (const [key, value] of Object.entries(data["users"])) {
        users[key] = value;
        // console.log(user[content[i]["name"]]);
      }
      for (const [key, value] of Object.entries(data["tweets"])) {
        tweets[key] = value;
        // console.log(user[content[i]["name"]]);
      }
      console.log("done");
    });
  return new Promise((res, rej) => {
    setTimeout(() => {
      res({
        tweets: {
          ...tweets,
        },
        users: {
          ...users,
        },
      });
    }, 500);
  });
  // checkNow();
  // console.log("data updated");
}

export function _getUsers() {
  return new Promise((res, rej) => {
    setTimeout(() => res({ ...users }), 1000);
  });
}

export function _getTweets() {
  return new Promise((res, rej) => {
    setTimeout(() => res({ ...tweets }), 1000);
  });
}

export function _saveLikeToggle({ id, hasLiked, authedUser }) {
  // console.log(users);
  return new Promise((res, rej) => {
    setTimeout(() => {
      tweets = {
        ...tweets,
        [id]: {
          ...tweets[id],
          likes:
            hasLiked === true
              ? tweets[id].likes.filter((uid) => uid !== authedUser)
              : tweets[id].likes.concat([authedUser]),
        },
      };

      res();
    }, 500);
  });
}

function generateUID() {
  return (
    Math.random().toString(36).substring(2, 15) +
    Math.random().toString(36).substring(2, 15)
  );
}

function formatTweet({ author, text, replyingTo = null }) {
  return {
    author,
    id: generateUID(),
    likes: [],
    replies: [],
    text,
    timestamp: Date.now(),
    replyingTo,
  };
}

export function _saveTweet({ text, author, replyingTo }) {
  return new Promise((res, rej) => {
    const formattedTweet = formatTweet({
      text,
      author,
      replyingTo,
    });

    setTimeout(() => {
      tweets = {
        ...tweets,
        [formattedTweet.id]: formattedTweet,
      };

      users = {
        ...users,
        [author]: {
          ...users[author],
          tweets: users[author].tweets.concat([formattedTweet.id]),
        },
      };

      res(formattedTweet);
    }, 1000);
  });
}
