import React, { Component } from "react";

class Menu extends Component {
  handleChange = (event) => {
    const html = document.documentElement;
    const theme = html.getAttribute("data-theme");
    if (!theme || theme === "light") {
      document.documentElement.setAttribute("data-theme", "dark");
      document.querySelector(".toggle-btn").textContent = "Dark Mode";
    } else {
      document.documentElement.setAttribute("data-theme", "light");
      document.querySelector(".toggle-btn").textContent = "Light Mode";
    }
  };

  showSetting = (event) => {
    const lists = document.querySelector(".modal.setting").classList;
    if (!lists.contains("active")) {
      lists.add("active");
    } else {
      lists.remove("active");
    }
  };

  closeSetting = (event) => {
    document.querySelector(".modal.setting").classList.remove("active");
  };

  returnSetting = (event) => {
    document.querySelector(".modal.setting").classList.remove("active");
  };
  render() {
    return (
      <>
        <div className="menu-container">
          <button className="setting-btn" onClick={this.showSetting}>
            <i className="icon setting"></i>
          </button>
        </div>
        <div class="ui mini modal setting">
          <div class="header">Setting</div>
          <div class="content">
            <div class="setting-cont">
              <span>Mode: </span>
              <button className="toggle-btn" onClick={this.handleChange}>
                Light mode
              </button>
            </div>
            <div class="setting-cont">
              <span>Max Count: </span>
              <select class="ui selection dropdown" id="max-count">
                <option selected value="10">
                  10
                </option>
                <option value="30">30</option>
                <option value="100">100</option>
              </select>
            </div>
          </div>
          <div class="actions">
            <div class="ui cancel button" onClick={this.closeSetting}>
              Close
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Menu;
